import React from 'react';
import './Header.css'

class Header extends React.Component{
  constructor() {
    super();
    this.title_text = "Templetes"
  }
  render(){
    return(
      <header className="homeheader">
        <h1 className="homeheadertext">{this.title_text}</h1>
      </header>
    );
  }
}

export default Header;
